.tooltip {
  display: inline-block;
  position: relative;
  text-align: left;
  @include u-bg(accent-warm-light);
  @include at-media(desktop) {
    position: relative;
  }
}

.tooltip .top {
  top: -20px;
  left: 50%;
  transform: translate(-50%, -100%);
  padding: 10px 20px;
  color: #444444;
  @include u-bg(accent-warm-light);
  font-weight: normal;
  font-size: 13px;
  border-radius: 8px;
  position: absolute;
  z-index: 99999999;
  box-sizing: border-box;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
  display: none;
  max-width: 200px;
  @include at-media(desktop) {
    min-width: 320px;
  }
}

.tooltip:hover .top {
  display: block;
}

.tooltip .top i {
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -12px;
  width: 24px;
  height: 12px;
  overflow: hidden;
}

.tooltip .top i::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  @include u-bg(accent-warm-light);
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
}
