}

.jcc-timeline-section__container {
  margin-left: auto ;
  margin-right: auto ;
  max-width: 87.5rem ;
  padding-left: 1rem ;
  padding-right: 1rem ;
}

@media all and (min-width: 64em) {
  .jcc-timeline-section__container {
    padding-left: 2rem ;
    padding-right: 2rem ;
  }
}

.jcc-timeline {
  margin-bottom: 0 ;
  margin-top: 0 ;
  list-style-type: none;
  padding-left: 0;
}

.jcc-timeline > li {
  margin-bottom: 0;
  max-width: unset;
}

@media all and (min-width: 55em) {
  .jcc-timeline {
    display: flex ;
    flex-wrap: wrap ;
    overflow-x: auto ;
    flex-wrap: nowrap ;
  }
}

.jcc-timeline > li {
  width: 100% ;
  padding-bottom: 2rem ;
}

@media all and (min-width: 55em) {
  .jcc-timeline > li {
    flex: 0 1 auto ;
    width: 33.33333% ;
  }
  .jcc-timeline > li > * {
    padding-top: 2rem ;
  }
}

@media all and (min-width: 55em) {
  .jcc-timeline > li {
    flex: 0 1 auto ;
    width: 25% ;
  }
}

@media all and (min-width: 55em) {
  .jcc-timeline .jcc-timeline-item {
    padding-right: 1.5rem ;
    padding-top: 4rem;
  }
}

@media all and (min-width: 55em) {
  .jcc-timeline .jcc-timeline-item__title:after {
    position: absolute;
    top: -1rem;
    right: auto;
    bottom: auto;
    left: auto;
    content: "";
    display: block;
    width: calc(100% + 1.5rem);
    height: 1px;
    border: 1px solid #005ea2;
    z-index: 1;
  }
  .jcc-timeline .jcc-timeline-item__title:before {
    position: absolute;
    top: -1.425rem;
    right: auto;
    bottom: auto;
    left: 0;
    background-color: #005ea2;
    border: 2px solid #005ea2;
    border-radius: 50%;
    content: "";
    height: 1rem;
    line-height: 1;
    text-align: center;
    width: 1rem;
    z-index: 2;
  }
}

@media all and (min-width: 55em) {
  .jcc-timeline .jcc-timeline-item__media {
    position: absolute ;
    top: 0 ;
  }
}

.jcc-timeline:not(.slick-initialized) li:last-of-type .jcc-timeline-item__title:after,
.slick-slide:last-of-type .jcc-timeline-item__title:after {
  width: 0 ;
}

.jcc-timeline__label {
  position: absolute;
  left: -999em;
}

.jcc-timeline__scroller--left,
.jcc-timeline__scroller--right {
  background-color: #e5a000 ;
  position: absolute ;
  bottom: 0 ;
  display: none ;
  top: 2.25rem;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  z-index: 10;
}

@media all and (min-width: 55em) {
  .jcc-timeline__scroller--left.is-active,
  .jcc-timeline__scroller--right.is-active {
    display: block ;
  }
}

.jcc-timeline__wrapper {
  position: relative ;
}

.jcc-timeline__scroller--left {
  left: 0 ;
}

.jcc-timeline__scroller--right {
  right: 0 ;
}

.jcc-timeline__scroller-icon {
  height: 1.75rem;
  width: 1.75rem;
}

.jcc-timeline__scroller-icon svg {
  display: block;
  margin: auto;
  height: 1.75rem;
  width: 1.75rem;
  fill: #1b1b1b;
}

.jcc-wayfinder-section {
