@media print {
  a:link:after {
    display: block;
    text-transform: none;
    font-size: 12px;
    content: attr(href);
  }
  a[href^="http"]:link:after {
    content: attr(href);
  }
  #js-header_menu--mobile {
    visibility: hidden !important;
  }
}

@media print {
  .show-for-print {
    display: block;
  }
  .jcc-switchboard,
  .jcc-jump-nav {
